or;\n      }\n    }\n\n    .table .thead-dark th {\n      color: inherit;\n      border-color: $table-border-color;\n    }\n\n    // Bootstrap specific changes end\n  }\n}\n","@import 'utils/scss/variables';\n@import 'utils/scss/mixins';\n\n/* .bs-datepicker */\n.bs-datepicker {\n  display: flex;\n  align-items: stretch;\n  flex-flow: row wrap;\n  background: $main-bg;\n  box-shadow: 0 0 10px 0 $main-box-shadow;\n  position: relative;\n  z-index: 1;\n\n  &:after {\n    clear: both;\n    content: '';\n    display: block;\n  }\n\n  bs-day-picker {\n    float: left;\n  }\n\n  /* button */\n  button:hover,\n  button:focus,\n  button:active,\n  input:hover,\n  input:focus,\n  input:active,\n  &-btns button:hover,\n  &-btns button:focus,\n  &-btns button:active,\n  &-predefined-btns button:active,\n  &-predefined-btns button:focus {\n    outline: none;\n  }\n\n  /* .bs-datepicker-head */\n  &-head {\n    min-width: 270px;\n    height: 50px;\n    padding: 10px;\n    border-radius: 3px 3px 0 0;\n    text-align: justify;\n\n    &:after {\n      content: \"\";\n      display: inline-block;\n      vertical-align: top;\n      width: 100%;\n    }\n\n    /* .bs-datepicker-head button */\n    button {\n      display: inline-block;\n      vertical-align: top;\n      padding: 0;\n      height: 30px;\n      line-height: 30px;\n      border: 0;\n      background: transparent;\n      text-align: center;\n      cursor: pointer;\n      color: $font-color-01;\n      transition: 0.3s;\n\n      &[disabled],\n      &[disabled]:hover,\n      &[disabled]:active {\n        background: $disabled-background;\n        color: $disabled-color;\n        cursor: not-allowed;\n      }\n\n      &.next,\n      &.previous {\n        border-radius: 50%;\n        width: 30px;\n        height: 30px;\n\n        span {\n          font-size: 28px;\n          line-height: 1;\n          display: inline-block;\n          position: relative;\n          height: 100%;\n          width: 100%;\n          border-radius: