pan.selected,\n        &.selected span {\n          color: $font-color-01;\n        }\n\n        span.is-other-month,\n        &.is-other-month span {\n          color: rgba(0, 0, 0, 0.25);\n        }\n\n        &.active {\n          position: relative;\n\n          &.select-start:before {\n            left: 35%;\n          }\n\n          &.select-end:before {\n            left: -85%;\n          }\n        }\n\n        span.active.select-start:after,\n        span.active.select-end:after,\n        &.active.select-start span:after,\n        &.active.select-end span:after {\n          content: \"\";\n          display: block;\n          position: absolute;\n          z-index: -1;\n          width: 100%;\n          height: 100%;\n          transition: 0.3s;\n          top: 0;\n          border-radius: 50%;\n        }\n\n        &:before,\n        span:before {\n          content: \"\";\n          display: block;\n          position: absolute;\n          z-index: -1;\n          top: 6px;\n          bottom: 6px;\n          left: -3px;\n          right: -2px;\n          box-sizing: content-box;\n          background: transparent;\n        }\n\n        &.active.select-start + td.active:before {\n          left: -20%;\n        }\n\n        &:last-child.active:before {\n          border-radius: 0 3px 3px 0;\n          width: 125%;\n          left: -25%;\n        }\n\n        span[class*=\"select-\"],\n        &[class*=\"select-\"] span {\n          border-radius: 50%;\n          color: $font-color-01;\n        }\n      }\n\n      /* .bs-datepicker-body table.days */\n      &.days {\n        td,\n        span {\n          &.active:not(.select-start):before,\n          &.in-range:not(.select-start):before {\n            background: $highlighted;\n          }\n        }\n\n        span {\n          width: 32px;\n          height: 32px;\n          line-height: 32px;\n\n          &.select-start {\n            z-index: 2;\n          }\n          &.is-highlighted.in-range:before {\n            right: 3px;\n            left: 0;\n          }\n          &.in-range.select-end:before {\n            right: 4px;\n            left: 0;\n          }\n        }\n\n        td {\n          &.select-start + td.select-end:before,\n          &.select-start + td.is-highlighted:before,\n          &.active + td.is-highlighted:before,\n          &.active + td.select-end:before,\n          &.in-range + td.is-highlighted:before,\n          &.in-range + td.select-end:before {\n            background: $highlighted;\n            width: 100%;\n          }\n        }\n      }\n\n      /* .bs-datepicker-body table.weeks */\n      &.weeks {\n        tr {\n          td {\n            &:nth-child(2).active:before {\n              border-radius: 3px 0 0 3px;\n              left: 0;\n              width: 100%;\n            }\n          }\n        }\n      }\n\n      &:not(.weeks) {\n        tr {\n          td{\n            &:first-child:before {\n              border-radius: 3px 0 0 3px;\n            }\n          }\n        }\n      }\n\n      &.years {\n        td {\n          span {\n            width: 46px;\n            height: 46px;\n            line-height: 45px;\n            margin: 0 auto;\n          }\n        }\n\n        tr:not(:last-child) {\n          td {\n            span {\n              margin-bottom: 8px;\n            }\n          }\n        }\n      }\n\n      &.months {\n        td {\n          height: 52px;\n\n          span {\n            padding: 6px;\n            border-radius: 15px;\n          }\n        }\n      }\n    }\n  }\n\n  /* .current-timedate */\n  .current-timedate {\n    color: $font-color-03;\n    font-size: 15px;\n    text-align: center;\n    height: 30px;\n    line-height: 30px;\n    border-radius: 20px;\n    border: 1px solid $border-color;\n    margin-bottom: 10px;\n    cursor: pointer;\n    text-transform: uppercase;\n    -moz-user-select: none;\n    -webkit-user-select: none;\n    -ms-user-select: none;\n\n    span:not(:empty):befor